.App {
  text-align: center;
  background: #ecf0f1;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr repeat(auto-fill, 1fr);
}

.App form {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-column: 1 / -1;
  grid-template-rows: 60px;
  border: 0;
  padding: 25px;
  grid-gap: 20px;
}

.todo-card {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr 1fr;
  grid-template-rows: 1fr repeat(auto-fit, 1fr);
  background: #fff;
  -webkit-box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.4);
          box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.4);
  margin: 25px;
  border: 0;
  grid-row: span 4;
}

.todo-input {
  display: block;
  font-size: 2rem;
  background: #fff;
  outline: none;
  -webkit-box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.7);
          box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.7);
  grid-gap: 0;
  text-align: center;
}

.todo-textarea-container {
  display: grid;
  font-size: 2rem;
  background: #fff;
  outline: none;
  -webkit-box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.7);
          box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.7);
  grid-template-columns: 5fr 1fr;
  text-align: center;
}

.todo-textarea {
  display: block;
  outline: none;
  font-size: 2rem;
  text-align: center;
  height: auto;
}

.textarea-btn {
  display: block;
  height: auto;
  font-size: 1.5rem;
  color: #fff;
  background: #2ecc71;
  border: 0;
  outline: none;
  margin: 0;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.textarea-btn:hover {
  background: #27ae60;
}

.card-title {
  grid-column: 1 / -1;
  grid-row: span 2;
  margin: 0;
  justify-self: center;
  -ms-flex-item-align: center;
      align-self: center;
}

.card-text {
  grid-column: 1 / -1;
  grid-row: span 8;
  background: #ecf0f1;
  margin: 0;
}

.card-text p {
  justify-self: center;
  -ms-flex-item-align: center;
      align-self: center;
  padding: 0 1px;
}

.card-btn {
  grid-column: 1 / -1;
  max-height: 100%;
  grid-row: span 1;
  background: #2ecc71;
  border: 0;
  outline: none;
  margin: 0;
  -webkit-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.card-btn:hover {
  background: #27ae60;
}

@media only screen and (max-width: 940px) {
  .App {
    grid-template-columns: 1fr 1fr;
    grid-gap: 10px;
  }

  .App form {
    display: grid;
    grid-template-columns: 1fr;
    grid-column: 1 / -1;
  }

  .todo-textarea-container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 1fr;
    -webkit-box-shadow: 0px 1px 10px rgba(0, 0, 0, 0);
            box-shadow: 0px 1px 10px rgba(0, 0, 0, 0);
    background: none;
    grid-gap: 10px;
  }

  .todo-input {
    font-size: 1.5rem;
  }

  .textarea-btn {
    font-size: 1.2rem;
    -webkit-box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.7);
            box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.7);
  }

  .todo-textarea {
    font-size: 1.5rem;
    -webkit-box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.7);
            box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.7);
    border: 0;
  }

  .todo-card {
    margin: 15px;
  }
}

@media only screen and (max-width: 500px) {
  .App {
    grid-template-columns: 1fr;
    grid-gap: 0px;
    font-size: 0.8rem;
  }

  .App form {
    border: 0;
    padding: 0px;
    margin-top: 20px;
    grid-gap: 20px;
  }

  .todo-card {
    margin: 0;
    margin-top: 15px;
  }

  .todo-input {
    font-size: 1.5rem;
  }

  .textarea-btn {
    font-size: 1.2rem;
  }

  .todo-textarea {
    font-size: 1.5rem;
  }
}


